
.service{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
   overflow: hidden;
   width: 100%;
   @media (min-width: 45em) {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 1rem;
    margin-top: -10rem;
    flex-direction: row;
    margin-bottom: 30px;
    width: 100%;
  }
  
    
}

.cards-service-content{
    display: flex;
    align-items: center;
    flex-direction: column;
    @media (min-width: 45em) {
        display: flex;
        justify-content: center;
        align-items: center;
        grid-gap: 1rem;
     
        flex-direction: row;
        margin-bottom: 30px;
        width: 100%;
      }
      

}

.card-service{
    background-color: #121214;
    border-top: 5px solid #FFF;
    width: 250px;
    height: 250px;
    margin: 15px;
}

.card-service:hover{
    border-top: 5px solid #b2121e;
    transition: .8s;
    margin-top: -7px;
 
}

.card-service img{
    width: 97px;
    height: 105px;
    object-fit: cover;
    margin: 0 auto;
    display: flex;
}

.text-content{
    margin: 0 auto;
   
}

.titlle-service{
    font-family: 'Kanit-Bold';
    font-size: 22px;
    color: #FFF;
    text-align: center;
}

.desc-service{
    text-align: center;
    font-size: 16px;
    color: #FFF;
    font-family: 'Kanit-Light';
    width: 90%;
    margin: 0 auto;
}